body {
  margin: 0;
}

.people {
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
  gap: 40px 30px;
  margin: 30px 35px 155px;
  display: grid;
}

@media (width >= 750px) {
  .people {
    margin-left: 160px;
    margin-right: 160px;
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 1fr);
    margin-bottom: 197px;
    margin-left: 55px;
    margin-right: 55px;
  }
}

@media (width >= 1500px) {
  .people {
    grid-template-columns: repeat(4, 1fr);
    margin-bottom: 197px;
    margin-left: 305px;
    margin-right: 305px;
  }
}

.person {
  width: 200px;
  transition: transform 1s;
  position: relative;
}

.person:hover {
  background-color: #eee;
  border-radius: 8px;
  transform: scale(1.2);
}

.person:hover .person__photo-img {
  border-radius: 25%;
}

.person:hover .person__extra {
  opacity: 1;
}

.person__photo {
  position: relative;
}

.person__photo-img {
  border-radius: 50%;
  width: 100%;
  margin-bottom: 8px;
  display: block;
}

.person__photo-rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 100%;
  display: flex;
  position: absolute;
  top: 84px;
  left: 158px;
}

.person__details {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.person__details-name {
  text-align: center;
  color: #2f4f4f;
  margin-bottom: 10.8px;
  font-size: 20px;
  font-weight: 700;
  line-height: 100%;
}

.person__extra {
  opacity: 0;
  font-size: 18px;
  font-weight: 400;
  transition: transform 1s;
  position: relative;
}
